@import url("https://fonts.googleapis.com/css2?family=Titillium+Web:wght@300;400;700&display=swap");
::selection {
	color: white;
	background: #64ffda;
  }
* {
	padding: 0;
	margin: 0;
	box-sizing: border-box;
	font-family: "Titillium Web", sans-serif;
	background-color: #0a192f;
	
     /* box-sizing: border-box; */
    /* outline: 1px solid limegreen !important; */
	
}

@keyframes shake {
    0% { transform: translate(1px, 1px) rotate(0deg); }
    10% { transform: translate(-1px, -2px) rotate(-1deg); }
    20% { transform: translate(-3px, 0px) rotate(1deg); }
    30% { transform: translate(3px, 2px) rotate(0deg); }
    40% { transform: translate(1px, -1px) rotate(1deg); }
    50% { transform: translate(-1px, 2px) rotate(-1deg); }
    60% { transform: translate(-3px, 1px) rotate(0deg); }
    70% { transform: translate(3px, 1px) rotate(-1deg); }
    80% { transform: translate(-1px, -1px) rotate(1deg); }
    90% { transform: translate(1px, 2px) rotate(0deg); }
    100% { transform: translate(1px, -2px) rotate(-1deg); }
  }
.lightest-slate{
	color:  #ccd6f6;
}

.nav-menu{
	/* box-shadow: rgba(0, 216, 232, 0.2) 0px 5px 10px; */
}
header {
	
	position: fixed;
	box-shadow: rgba(44, 223, 255, 0.35) 0px 5px 15px;
	display: flex;
	align-items: center;
	justify-content: space-between;
	height: 80px;
	width: 100%;
	padding: 0 2rem;
	background-color:#0a192f;
	color: white;
	z-index:100;
}


.colorP{
	color: #64ffda;
	font-size: larger;
}

nav a {
	margin-top: 10px;
	color: white;
	padding: 10px;
	text-decoration: none;
}

.active {
	color: #64ffda;
	font-size: larger;
	transition: 0.5s;
}


header .nav-btn {
	padding: 5px;
	cursor: pointer;
	background: transparent;
	border: none;
	outline: none;
	color: white;
	visibility: hidden;
	opacity: 0;
	font-size: 1.8rem;
}
.secNav{
	display: flex;
	justify-content: space-around;
}

header div,
nav {
	display: flex;
	align-items: left;
}
.hiIcon{
	height: 25px;
}
.Home{
	padding: 10px 50px;
	background-color: #0a192f;
	color: white;
}
.light-slate{
	color: #ccd6f6;
}
.slate{
	color: #8892b0;
}
.largHeading{
	font-size: 60px;
}
.summery{
	font-size: larger;
	width: 80%;
	color: #8892b0;
	margin: 20px 5px;
}
.space{
	height: 100px;
	width: 100%;

}

.comman{
	padding: 20px 50px;
	color: white;
}
.aboutFlex{
	display: flex;
	width: 100%;
	width: fit-o;
	justify-content: space-around;
}
.aboutFlex  img{
	border-radius: 5%;
	height: 100%;
	width: 100%;
	/* background-color: #6effdd; */
	/* opacity: 0.7; */
	/* margin-top: -20px; */
	/* margin-left: -10px; */
	/* position: absolute; */
	/* filter: brightness(100%); */

	
}

.imgContainer{
		/* filter: brightness(80%); */
	border-radius: 5%;
	height: 300px;
	width: 245px;
	margin: auto;
	/* z-index: 1; */
	/* background-color: #64ffda; */
	border: #28dfb1 solid 5px;
}

.imgContainer:hover{
	
	border: #28dfb1 solid 1px;
}
.nameColor{
	color:#64ffda ;
	width: fit-content;
	background: transparent;
}
.contactInfo{
	display:flex; 
	gap:10px;
	/* margin:10px; */
}
.contactInfo > p , a{
	color: #8892b0;
	margin-top: -5px;
}
.aboutFlex > a{
	color: white;
}
.skillDiv{
	gap: 50px;
	display: grid;
	grid-template-columns: repeat(4,1fr);
	margin: auto;
	width: 90%;
	font-size: larger;
}
.skill-icon{
	font-size: 40px;
}
.skillDiv > div:hover{
	padding: 5px;
	transition: 0.5s;
	animation: shake 0.5s;
    animation-iteration-count: infinite;
	border-radius: 5%;
	box-shadow: rgba(39, 241, 255, 0.2) 0px 5px 10px;
}
.javascript{
	color: #8892b0;
}
.javascript:hover{
	color: #28dfb1;
	transition: 0.5s;
	font-size: larger;
	
}
.other-skill{
	/* color: #64ffda; */
	/* font-size: small; */
	/* border: #28dfb1 solid 1px; */
	/* padding: 5px; */
	/* width: fit-content; */
	margin: 20px 5px;
	text-decoration: underline #207d64;
}
.projectContainer{
	display: flex;
	padding: 3%;
}
.projectImg {
	width: 100%;
	opacity: 0.6;
	border-radius: 10px;
}
.profileImg{
	opacity: 0.7;
	border-radius: 10px;
}
.profileImg:hover, .projectImg:hover{
	opacity: 1;
	/* animation: shake 0.5s; */
    /* animation-iteration-count: infinite; */
    /* border: 2px solid #64ffda; */
}
.pimg{
	width: 50%;
	height: fit-content;
	margin-left: -10%;
	border-radius: 10px;
	background-color: #28dfb1;
	/* opacity: 0.5; */
	
}
.profileCon{
	width: 150px;
	height: fit-content;
	/* margin-left: -10%; */
	border-radius: 10px;
	background-color: #28dfb1;
}
.pInfo{
	z-index: 1;
	background: transparent;
	width: 50%;
	margin-top: 50px;
}
.pInfo  h3 ,h2 , div {
	background: transparent;
	color: #a8b2d1;
}
.pInfo > h1{
	text-decoration: underline 4px #8892b0;
	background: transparent;
}
.pInfo > div{
	display: flex;
	margin: 20px 0px;
	gap: 20px;
}
.pInfo svg:hover{
	color: #28dfb1;
	height: 50px;
	transition: 0.3s;
}

.pdesc{
	padding: 40px 10px;
	border-radius: 5px;
	background-color: #112240 ;
	margin: 10px 0;
	font-size: large;
	color: #8892b0;
	box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}
.resumeDown{
	margin: 10px;
	border: #28dfb1 3px solid;
	padding: 5px;
	border-radius: 10px;
	width: fit-content;
	font-size: medium;
	color: white;
	text-decoration: none;
}
.resumeDown:hover{
	border: white ;
	color: #28dfb1;
	transform: 0.5s;
	box-shadow: rgba(5, 242, 255, 0.24) 0px 3px 8px;
}

@media only screen and (max-width: 1024px) {
	.resumeDown{
	font-size: x-small;
	/* padding: 5px; */
	border: 2px solid #28dfb1 ;
	}
	.comman{
		padding: 20px;
	}
	.Home{
		padding: 20px;
	}


	.pimg{
		width: 100%;
		margin: 0px;
	}
	.pInfo h1, h2, h3{
		/* width: 100%; */
		font-size: small;
	}
	.pInfo{
		width: 100%;
	}
	.pdesc{
		padding: 10px;
		width: 100%;
	}
	.projectContainer{
		flex-direction: column;
		padding: 5px;
		
		width: 100%;
	}
	.pInfo{
		margin:0px
	}
	.skillDiv{
		grid-template-columns: repeat(2,1fr);
	}
	.aboutFlex img{
		/* height: 150px; */
		/* width: 100px; */
		/* margin: auto; */
		/* width: fit-content; */
	}
	.pdesc{
		font-size: smaller;
	}
	.imgColor{
		height: 200px;
		width: 155px;
	}
	.imgContainer{
		margin-top: 20px;
		margin-left: auto;
		width: 160px;
		height: 200px;
	}

	.largHeading{
		font-size: 30px;
	}
	.summery{
		font-size: small;
		width:100%;
		
	}
	header .nav-btn {
		visibility: visible;
		opacity: 1;
	}

	header nav {
		position: fixed;
		top: -100vh;
		left: 0;
		height: 100%;
		width: 100%;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		gap: 1.5rem;
		background-color: #0a192f;
		transition: 1s;
	}

	header .responsive_nav {
		transform: translateY(100vh);
	}

	nav .nav-close-btn {
		position: absolute;
		top: 2rem;
		right: 2rem;
	}

	nav a {
		font-size: 1.5rem;
	}
	.aboutFlex{
	display: flex;
	flex-direction: column-reverse;
	justify-content: space-between;
}
.javascript{
	font-size: smaller;
}
}
